@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Charm:400,700&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 2rem;
    font-family: "Roboto", sans-serif;
}

:root {
    --clr-light: #fff;
    --clr-dark: #000;
    --clr-accent: #f1af4bd2;

    --bs-sml: 0.125rem 0.125rem 0.25rem #bbb;
    --bs-med: 0.25rem 0.25rem 0.75rem #aaa, 0.125rem 0.125rem 0.25rem #bbb;
}

.app-title {
    font-size: 4rem;
    background: -webkit-gradient(linear, left top, left bottom, from(#fcf800), to(#dd0000));
    background: linear-gradient(#fcf800, #dd0000);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.btn-container {
    width: 100%;
    padding-right: 30%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 3rem;
}

.units-btn {
    box-shadow: var(--bs-sml);
}

/* Today */
.today__container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 5rem;
}

.today__card {
    width: 18rem;
    box-shadow: var(--bs-med);
    background-color: var(--clr-accent);
}

/* Forecast */
.forecast__div {
    overflow: visible;
    max-height: 250px;
}

.forecast__accordion,
.forecast__card {
    border: 1px solid var(--clr-accent);
    border-radius: 5px;
    box-shadow: var(--bs-med);
    background-color: var(--clr-accent);
}

.forecast__accordion--title,
.today__card--title {
    font-size: 1.5rem;
}

.forecast__accordion--subtitle,
.today__card--subtitle {
    font-size: 1.1rem;
}

.forecast__accordion--btn {
    border: 0.5px solid lightgray;
    border-radius: 5px;
    background-color: var(--clr-light);
    color: var(--clr-accent);
    font-size: 0.8rem;
}

.forecast__accordion--btn:hover {
    text-decoration: none;
    background-color: var(--clr-accent);
    color: var(--clr-light);
    border: 1px solid lightgray;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.forecast__accordion--btn:focus {
    text-decoration: none;
}

.forecast__accordion--body {
    padding: 0;
}

.hour__container,
.hour__card {
    padding: 0;
}

.hour__card--body {
    padding: 10px;
}

.hour__card--title {
    font-size: 1.1rem;
}

.hour__card--subtitle {
    font-size: 1rem;
}

.hour__card--text {
    font-size: 0.8rem;
}

/* Footer */

.footer {
    position: absolute;
    top: 10px;
    right: 5rem;
    height: 50px;
    line-height: 50px;
    font-weight: bold;
    font-size: 1.1rem;
    font-family: "Charm", cursive;
}

.footer a {
    text-decoration: none;
    color: var(--clr-accent);
    font-weight: bold;
}

.footer span {
    font-size: 1rem;
    font-weight: 400;
}

.git-icon {
    font-size: 1.5em;
}

@media (max-width: 1468px) {
    .hour__container {
        display: block;
        min-width: 95%;
    }
}
@media (max-width: 1275px) {
    .forecast__card--container {
        min-width: 30%;
        margin: 1.5em;
    }
}
@media (max-width: 850px) {
    .forecast__card--container {
        min-width: 85%;
        margin: 1.5em;
    }
}

